import "./App.css";
import React, {  } from "react";
import DownloadLink from "react-download-link";

class Coord {
  constructor(lat, lon) {
    this.lat = lat;
    this.lon = lon;
  }
}

export default function App() {
  /* <Components/> */
    
  class QuestForm extends React.Component {
    constructor(props) {
      super(props);
      this.state = { inputText: ""};
      
      this.handleFormSubmit = this.handleFormSubmit.bind(this);
    }
    
    createGpx (temp) {
      let stringFile = "";
      stringFile += '<?xml version="1.0" encoding="utf-8" standalone="yes"?>\n';
      stringFile += "<gpx>\n";
      stringFile += "<trk>\n";
      stringFile += "<trkseg>\n";
      
      temp = this.getCoords(temp)
      console.log(temp)

      temp.forEach(i => {
        let [lat, lon] = i[0].split(",")
  
        stringFile += `<trkpt lat="${parseFloat(lat)}" lon="${parseFloat(lon)}"></trkpt>\n`;
      });
      
      stringFile += `</trkseg>\n`;
      stringFile += `</trk>\n`;
      stringFile += `</gpx>\n`;
      return stringFile;
    }

    getCoords () {
      const coordRegex = /[0-9]{1,3}\.[0-9]{3,14},.[0-9]{0,3}\.[0-9]{3,14}/g
      const text = this.state.inputText
      return [...text.matchAll(coordRegex)]
    }

    handleFormSubmit(e) {
      e.preventDefault();
      
      const coords = this.getCoords()
      let temp = []
      
      coords.forEach(i => {
        const [lat, lng] = i[0].split(",")
        temp = [...temp, new Coord(lat, lng)]
      })
      this.setState({ coords: temp })

      document.querySelector("a").click();
    }
    
    
    render() {
      return (
        <>
          <form 
            onSubmit={(e) => this.handleFormSubmit(e)}
            className="gpx-form"
            >
            <input
              type="text"
              value={this.state.inputText}
              onChange={(e) => {
                this.setState({ inputText: e.target.value });
              }}
              />
            <input type="submit" value="Get .gpx file" className="gpx-submit"/>
          </form>
          <DownloadLink
          label=""
          filename="pokemon.gpx"
          exportFile={() => this.createGpx(this.state.inputText)}
          />
        </>
      );
    }
  }
  
  /* <Components/> */
  
  
  /* Functions */
  // const copyToClipboard = (text) => {
    //   let textField = document.createElement('textarea')
    //   textField.innerText = text
    //   document.body.appendChild(textField)
    //   textField.select()
    //   document.execCommand('copy')
    //   textField.remove()
    
    // }
    
    return (
      <>
      <QuestForm />
      
      {/* <button onClick={ e => console.log(coords) }>Print coords</button> */}
    </>
  );
}

